.page-footer {
  margin: 0 auto;
  text-align: center;
  box-sizing: border-box;

  @media (min-width: $tablet-width) {
    width: 768px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    align-items: baseline;
    padding-left: 45px;
    padding-right: 45px;
  }

  @media (min-width: $desktop-width) {
    width: 1200px;
    background-color: $white;
  }
}

.page-footer__tag {
  font-weight: 700;
  font-size: 21px;
  line-height: 26px;

  @media (min-width: $tablet-width) {
    text-align: left;
    padding-left: 14px;
  }

  @media (min-width: $desktop-width) {
    padding-left: 75px;
  }
}

.page-footer__develop {
  display: flex;
  align-items: center;
  justify-content: space-around;
  margin-top: 30px;

  @media (min-width: $tablet-width) {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
  }
}

.page-footer__develop-paragraph {
  font-weight: 400;
  font-size: 14px;
  line-height: 26px;

  @media (min-width: $tablet-width) {
    margin-right: 14px;
  }
}

.page-footer__develop-link {
  @media (min-width: $tablet-width) {
    margin-right: 14px;
  }

  @media (min-width: $desktop-width) {
    padding-right: 64px;
  }
}

.page-footer__list {
  @include list-style;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: 0;
  margin-top: 35px;

  @media (min-width: $desktop-width) {
    margin-top: 31px;
  }
}

.page-footer__item {
  display: flex;
  margin-left: 5px;
}

.page-footer__link {
  @include links;
  margin: auto;
  text-decoration: none;
  display: flex;
  background-color: #81b3d2;
  width: 47px;
  height: 48px;
  align-content: center;
  align-items: center;
  justify-content: center;

  &:hover {
    background-color: $blue-hover;
  }

  &:active {
    background-color: $blue-down;
    color: $white2;

    .page-footer__link-icon {
      opacity: 0.6;
    }
  }
}

.page-footer__develop-icon:hover {
  fill: $blue;
}

.page-footer__develop-icon:active {
  fill: $blue;
  opacity: 0.3;
}
