/*main*/
@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("PT Sans"), url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
}
@font-face {
  font-family: "PT Sans";
  font-style: bold;
  font-weight: 700;
  font-display: swap;
  src: local("PT Sans"), url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
}
* {
  box-sizing: border-box;
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.page-body {
  font-family: "PT Sans", "Arial", sans-serif;
  font-style: normal;
  color: #000000;
  background-color: #ffffff;
  min-width: 320px;
  min-height: 100%;
  margin: 0;
  text-transform: uppercase;
  display: grid;
  grid-template-rows: min-content max-content min-content;
}
@media (min-width: 1280px) {
  .page-body {
    background-color: #e7e5e5;
  }
}

.page-header {
  margin: 0 auto;
  width: 100%;
  text-align: center;
  box-sizing: border-box;
  background-color: #ffffff;
}
@media (min-width: 1280px) {
  .page-header {
    max-width: 1200px;
    background-color: #ffffff;
  }
}

.page-header__logo {
  margin: 0 auto;
  display: block;
  position: relative;
  z-index: 1;
  width: 187px;
}
@media (min-width: 768px) {
