    z-index: 10;
  }
}

.page-header__logo-icon-fill {
  display: none;
}
@media (min-width: 1280px) {
  .page-header__logo-icon-fill {
    display: block;
    margin: 0 auto;
    top: 0;
    z-index: -1;
    position: absolute;
  }
}

.page-main {
  margin: 0 auto;
}
@media (min-width: 1280px) {
