  }
}

.main-nav__toggle {
  position: absolute;
  top: -71px;
  right: 20px;
  z-index: 2;
  display: block;
  width: 22px;
  height: 22px;
  background-color: #ffffff;
  cursor: pointer;
  border: none;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}

.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  width: 22px;
  height: 4px;
  background-color: #81b3d2;
  box-shadow: 0 8px 0 0 #81b3d2, 0 16px 0 0 #81b3d2;
}

.main-nav--closed .main-nav__toggle:hover {
  opacity: 0.6;
}
.main-nav--closed .main-nav__toggle:active {
  opacity: 0.3;
}

.main-nav--opened {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}

