.review {
  text-align: center;
  padding: 0 20px 0 20px;
  position: relative;
  max-width: 320px;
  margin: auto;
  margin-bottom: 38px;
}
@media (min-width: 768px) {
  .review {
    max-width: 768px;
    padding: 0;
    margin: 0 auto;
    margin-bottom: 13px;
    margin-top: 0;
  }
}
@media (min-width: 1280px) {
  .review {
    margin-top: 82px;
    margin-bottom: 20px;
  }
}

.review::before {
  position: absolute;
  content: "";
  background-image: url("../img/intro-triangle.svg");
  background-repeat: no-repeat;
  width: 320px;
  height: 57px;
  top: -89px;
  left: 0;
}
@media (min-width: 768px) {
  .review::before {
    background-image: url("../img/intro-triangle-tablet.svg");
    width: 768px;
    top: -119px;
    left: -60px;
  }
}
@media (min-width: 1280px) {
  .review::before {
    background-image: url("../img/intro-triangle-desktop.svg");
    width: 1200px;
    top: -168px;
    left: -216px;
  }
}

